export default {
	data() {
		return {
			showPassword: true,
			login: {
				mobile: "",
				password: ""
			}
		}
	},
	methods: {
		changePassword() {
			let that = this;
			that.showPassword = !that.showPassword;
		},
		clearIcon() {
			let that = this;
			that.login.mobile = "";
		},
		toLogin() {
			let that = this;
			let mobile = that.login.mobile;
			let password = that.login.password;
			if (!that.$utils.common.phoneTest.test(mobile)) {
				uni.showToast({
					title: "请输入正确的手机号",
					icon: "none"
				})
			} else if (password.length < 6) {
				uni.showToast({
					title: "密码需六位以上字符",
					icon: "none"
				})
			} else {
				uni.showToast({
					title: "登录成功",
					icon: "success"
				})
			}
		},
		register(){
			let that = this;
			uni.navigateTo({
				url:'./register'
			})
		},
		back() {
			uni.navigateBack({
				delta:1
			})
		},
	}
}
